@import url(https://fonts.googleapis.com/css2?family=Kanit&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  text-align: center;
  min-height: 100vh;
}

#root {
  min-height: 100vh;
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
box-sizing: inherit;
}
* {
  font-family: 'Roboto', sans-serif;
}

a {
  text-decoration: none;
  color: black;
}
.sidebar {
  text-align: left;
  min-width: 200px;
  padding-left: 2.5em;
  background-color: rgba(0, 0, 0, 0.1);
  min-height: 100%;
}

.sidebar h2 {
  margin: 1.5em 0;
}

.sidebar a {
  transition: 0.2s;
}

.sidebar .selected {
  font-style: italic;
  padding-left: 0.5em;
  color: rgb(20, 80, 150);
}
.cover {
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
}

.cover img {
  height: 350px;
}

.cover-caption {
  background-color: rgb(49, 148, 206);
  color: white;
  margin: 0;
  text-align: center;
  padding: 0.2em 0;
  font-family: 'Kanit', sans-serif;
  font-weight: bold;
  font-size: 1.2em;
}

.store {
  display: -webkit-flex;
  display: flex;
}

.store-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-gap: 30px;
  gap: 30px;
  padding: 2.5em 0.5em;
  background-color: rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  margin: 1.5em;
  width: 100%;
  justify-items: center;
  -webkit-align-items: center;
          align-items: center;
}

.store-item {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
}

.store-item-caption {
  text-align: center;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  margin: 0.5em 0 0 0;
}

.store-price {
  font-weight: bolder;
  font-size: 1.1em;
}
.counter button {
  background-color: rgb(255, 208, 0);
  border: none;
  border-radius: 5px;
  box-shadow: -1px 1px 3px rgba(0, 0, 0, 0.5);
  padding: 0.2em 0.7em;
  font-size: 1em;
}

.counter input {
  border: none;
  margin: 0 1em;
  border-radius: 5px;
  text-align: center;
  width: 30px;
  box-shadow: -1px 1px 3px rgba(0, 0, 0, 0.5);
  padding: 0.2em 0;
  font-size: 1em;
}

.counter input::-webkit-outer-spin-button,
.counter input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.counter button:active {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}

.counter button:hover {
  background-color: rgba(255, 208, 0, 0.5);
}

.cart {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  padding: 2em 0;
}

.cart-items-wrapper,
.cart-checkout-wrapper,
.cart-empty {
  background-color: rgba(0, 0, 0, 0.1);
  min-width: 60vw;
  border-radius: 5px;
}

.cart-items-wrapper {
  padding: 1.5em 2em;
}

.cart-item {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.cart img {
  height: 200px;
}

.cart .cover-caption {
  font-size: 0.9em;
}

.cart-price-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: flex-end;
          align-items: flex-end;
}

.cart .counter button,
.cart .counter input {
  font-size: 0.7em;
}

.cart-price {
  font-weight: bolder;
  font-size: 1.1em;
  margin: 0;
}

.cart-counter-controls {
  display: -webkit-flex;
  display: flex;
  margin-bottom: 0.5em;
}

.cart-delete,
.cart-checkout-btn {
  border: none;
  background-color: rgb(255, 208, 0);
  box-shadow: -1px 1px 3px rgba(0, 0, 0, 0.5);
  padding: 0.2em 0.7em;
  border-radius: 5px;
}

.cart-delete {
  margin-left: 1em;
}

.cart-delete:hover,
.cart-checkout-btn:hover {
  background-color: rgba(255, 208, 0, 0.5);
}

.cart hr {
  margin: 1em 0;
}

.cart-checkout-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 2em;
  padding: 1.5em;
}

.cart-checkout-wrapper p {
  margin: 0;
  font-size: 1.3em;
  font-weight: bolder;
}

.cart-subtotal-wrapper {
  display: -webkit-flex;
  display: flex;
}

.cart-subtotal-label {
  padding-right: 0.5em;
}

.cart-checkout-btn {
  font-size: 1.3em;
}

.home {
  padding: 1em 2em;
}

.home h2 {
  text-align: left;
  margin: 1em 0 1.5em 0.3em;
  background-color: rgb(20, 80, 150);
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  padding: 0.5em;
  border-radius: 5px;
  font-size: 1.2em;
}

.home h2 a {
  color: white;
}

.release-wrapper {
  background-color: rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  padding: 0.5em 1.5em 1.5em 1.5em;
  margin: 1.5em 0;
}

.release-subwrapper {
  display: -webkit-flex;
  display: flex;
  overflow: auto;
}

.release-subwrapper a {
  margin: 0 1.5em;
}
.details {
  display: -webkit-flex;
  display: flex;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 1.5em auto;
  padding: 2em 0 2em 2em;
  background-color: rgba(0, 0, 0, 0.1);
  border-radius: 5px;
}

.details section {
  margin: 0 3em;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  max-width: 50vw;
}

.details-title {
  font-weight: bolder;
  font-size: 2em;
  margin: 0;
}

.details-summary {
  text-align: justify;
  text-justify: auto;
}

.details-price {
  margin-top: 1em;
  font-weight: bolder;
  font-size: 1.2em;
}

.details-add-btn {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  display: -webkit-flex;
  display: flex;
  -webkit-align-self: center;
          align-self: center;
  padding: 0.5em 1em;
  font-size: 1em;
  background-color: rgb(255, 208, 0);
  border: none;
  border-radius: 5px;
  box-shadow: -1px 1px 3px rgba(0, 0, 0, 0.5);
}

.details-add-btn:active {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}

.details-add-btn:hover {
  background-color: rgba(255, 208, 0, 0.5);
}

nav {
  background-color: rgb(20, 80, 150);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 2em;
  color: white;
}

nav h1 {
  font-style: italic;
  margin: 0 0 0.1em 0;
}

nav ul {
  display: -webkit-flex;
  display: flex;
  list-style: none;
  margin: 0;
}

nav a {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 1em 2em;
  color: white;
}

nav a:hover {
  background-color: white;
  cursor: pointer;
  color: black;
}

nav img {
  height: 30px;
  -webkit-filter: invert(100%);
          filter: invert(100%);
}

nav a:hover img {
  -webkit-filter: none;
          filter: none;
}

.nav-cart {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  position: relative;
}

.nav-cart span {
  font-size: 0.5em;
  position: absolute;
  width: 10px;
  left: 12px;
  top: 7.5px;
}

footer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  padding: 1.5em 0;
  background-color: rgb(20, 80, 150);
  color: white;
}

footer p {
  margin: 0;
}

footer div {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 0.5em;
}

footer a {
  margin: 0 0.5em;
}
